* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: sans-serif;
}

#root {
    display: flex;
    justify-content: center;
}

.wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 90vw;
    max-width: 1440px;
    height: 100vh;
    border: 1px solid black;
    background-color: grey;
}

.main {
    display: flex;
    flex-direction: row;
    width: 100%;
    flex-grow: 1;
    padding-top: 4vh;
}

.main aside {
    width: 20%;
}


.pages {
    display: flex;
    width: 80%;
}
footer {
    width: 100%;
    height: 50px;
    color: white;
    letter-spacing: 4px;
    background-color: black;
    text-align: center;
    line-height: 50px;
}


.activ {
    border-left: 5px yellow;
}

.motorcycle-wrapper , .bike-wrapper , .car-wrapper {
    display: flex;
    flex-direction: column;
    padding-left: 20px;
}
header {
    width: 100%;
    height: 300px;
}

.headerImage {
    width: 100%;
    height: 100%;
    object-fit: cover;
}
nav {
    height: 100%;
}

ul {
    width: 100%;
    height: 100%;
    list-style: none;
}

a {
    display: block;
    padding: 10px;
    transition: .3s;
    cursor: pointer;
    text-decoration: none;
    color: black;
}

a:hover.navigationLink {
    -webkit-transform: translateX(20px);
            transform: translateX(20px);
}

a.active.navigationLink  {
    
    border-left: 6px solid yellow;
}

.products-item:hover {
    border-left: 6px solid black;
}


.startPage {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.startPage h1 {
    font-size: 24px;
}

.article-title {
    font-size: 22px;
}

.article-author {
    margin-top: 5px;
    font-size: 14px;
}

.article-content {
    margin-top: 15px;
}
.contact-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}

form {
    display: flex;
    flex-direction: column;
    grid-gap: 20px;
    gap: 20px;
}

form input {
    height: 30px;
    border-radius: 8px;
    border: none;
    background-color: white;
    transition: .1s;
}

form input:active {
    -webkit-transform: translateY(10%);
            transform: translateY(10%);
    box-shadow: 0 0 8px black;
}
.adminPanel-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    grid-gap: 30px;
    gap: 30px;
}

.inputs-wrapper {
    display: flex;
    justify-content: center;
    width: 100%;
}

.adminPanel-wrapper button {
    height: 30px;
    border-radius: 8px;
    border: none;
    background-color: white;
    width: 150px;
    transition: .1s;
}

.adminPanel-wrapper button:active {
    -webkit-transform: translateY(10%);
            transform: translateY(10%);
    box-shadow: 0 0 8px black;
}
.productPage {
    display: flex;
    flex-direction: column;
    grid-gap: 20px;
    gap: 20px;
}

.productPage a {
    padding:unset;
}
.footer {
    display: flex;
    justify-content: center;
    width: 100%;
    height: 50px;
    color: white;
    background-color: black;
}
.footer-inside-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    width: 70%;
    height: 50px;
    font-size: 14px;
}
